<template>
  <!-- 如果数据从网络获取，则自动轮播到最后就不轮播了,只要在swiper上加v-if这句 -->
   <swiper :options="swiperOption" class="banner" v-if="banners.length>0">
      <!-- slides -->
      <swiper-slide v-for='value in banners' :key='value.bannerId' class="item">
        <a :href="value.url">
          <img v-lazy="value.pic" alt="">
        </a>
      </swiper-slide>
      <!-- Optional controls -->
    <div class="swiper-pagination"  slot="pagination"></div>
  </swiper>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import { swiper, swiperSlide } from 'vue-awesome-swiper'
export default {
  name: 'Banner',
   components: {
    swiper,
    swiperSlide
  },
  props:{
      banners:{
          type:Array,
          default:()=>[],
          required:true
      }
  },
  data() { 
        return {
            // some swiper options/callbacks
                    // 所有的参数同 swiper 官方 api 参数
                    // ...
            swiperOption: {
                loop: true, // 循环模式选项
                autoplay: {
                delay: 1000, // 自动切换的时间间隔，单位ms
                stopOnLastSlide: false, // 当切换到最后一个slide时停止自动切换
                disableOnInteraction: false // 用户操作swiper之后，是否禁止autoplay。
                },
            // 如果需要分页器
                pagination: {
                el: '.swiper-pagination'
                },
                observer: true,
                observeParents: true,
                observeSlideChildren: true
            },
        }
    }
 }
</script>

<style lang="scss" scoped>
  .banner{
   height: 292px;
    .item{
      img{
         width: 100%;
      }
    }
   
  }
</style>
<style lang="scss">
@import '../../assets/css/mixin';
// 要覆盖swiper样式，则scoped不行
 .banner{
   width: 100%;
   .swiper-pagination-bullet{
      width: 16px;
      height:16px;
      background-color: #fff;
      opacity: 1;
    }
   .swiper-pagination-bullet-active{
     @include bg_color();
   }
 }
</style>