<template>
  <div class="recommend">
    <!-- 套div是为了overflow：hidden不设给详情界面 -->
    <div class="recommend-warpper">
      <ScrollView>
          <div>
              <Banner :banners="banners"></Banner>
              <!-- 注意加单引号 -->
              <Personalized :personalized="personalized" :title="'推荐歌单'" @select="fatherSelectItem" :type="'personalized'"></Personalized>
              <Personalized :personalized="albums" :title="'最新专辑'" @select="fatherSelectItem" :type="'albums'"></Personalized>
              <SongList :songs=songs></SongList>
          </div>
      </ScrollView>
    </div>
    
    <transition>
       <router-view></router-view>
    </transition>
  </div>
</template>

<script>
import {getBanner, getPersonalized,getNewAlbum,getNewSong} from "../api/index";
import Banner from "../components/Recommend/Banner";
import Personalized from "../components/Recommend/Personalized";
import SongList from "../components/Recommend/SongList";
import ScrollView from "../components/ScrollView";
export default {
  name: 'Recommend',
  components:{
    Banner,
    Personalized,
    SongList,
    ScrollView,
  },
  data(){
    return{
      banners:[],
      personalized:[],
      albums:[],
      songs:[],
    }
  },
  methods:{
    //记得写id
    fatherSelectItem(id,type){
      this.$router.push({
        path:`/recommend/detail/${id}/${type}`
      })
    }
  },
  created(){
    getBanner()
      .then((data)=>{
        // console.log(data)
        this.banners=data.banners;
      })
      .catch(function(err){
        console.log(err)
      });
     getPersonalized()
      .then((data)=>{
        // console.log(data)
        this.personalized=data.result;
      })
      .catch(function(err){
        console.log(err)
      });
      getNewAlbum()
      .then((data)=>{
        // console.log(data.albums.splice(6))
        this.albums=data.albums.splice(6);
      })
      .catch(function(err){
        console.log(err)
      });
      getNewSong()
      .then((data)=>{
        // console.log(data.result);
        this.songs=data.result;
      })
      .catch(function(err){
        console.log(err)
      })
  },
  
 }
</script>

<style lang="scss" scoped>
.recommend{
  // width: 100%;
  // height: 100%;
  position: fixed;
  left: 0;
  right: 0;
  top:200px;
  bottom: 0;
  // overflow: hidden;
  .recommend-warpper{
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
}
.v-enter{
  transform:translateX(100%);
}
.v-enter-active{
  transition:transform 1s;
}
.v-enter-to{
  transform:translateX(0);

}
.v-leave{
  transform:translateX(0);

}
.v-leave-active{
  transition:transform 1s;
}
.v-leave-to{
  transform:translateX(100%);

}
</style>