<template>
  <div class="song">
      <div class="song-top">
          <h3>最新音乐</h3>
      </div>
      <ul class="song-list">
          <li v-for="value in songs" :key="value.id" class="item" @click="selectMusic(value.id)">
              <img v-lazy="value.song.album.picUrl" alt="">
              <div>
                  <h3>{{value.song.artists[0].name}}</h3>
                  <p>{{value.song.album.name}}</p>
              </div>
          </li>
      </ul>
  </div>
</template>

<script>
import {mapActions} from 'vuex';
export default {
  name: 'SongList',
  props:{
      songs:{
          type:Array,
          default:()=>[],
          required:true
          },
  },
  methods:{
      ...mapActions([
          'setFullScreen',
          'setMiniPlayer',
          'setSongDetail'
      ]),
      selectMusic(id){
          this.setFullScreen(true);
          this.setSongDetail([id]);
      }
  },
  data() { 
    return {

    }
  }
 }
</script>

<style lang="scss" scoped>
@import '../../assets/css/mixin';
@import '../../assets/css/variable';
.song{
    .song-top{
        width: 100%;
        height: 84px;
        line-height: 84px;
        @include bg_sub_color();
        padding: 0 20px;
        border-bottom: 1px solid #ccc;
        h3{
            @include font_size($font_large);
            font-weight:bold;
            @include font_color();

        }
    }
    .song-list{
        .item{
            width: 100%;
            height: 150px;
            display: flex;
            align-items: center;
            padding:10px  20px;
            border: 1px solid #ccc;
            @include bg_sub_color();

            img{
                width: 150px;
                height: 150px;
                border-radius: 10px;
                margin-right: 20px;
            }
            div{
                h3{
                    @include font_size($font_medium);
                    @include font_color();
                    
                }
                p{
                    @include font_size($font_small);
                    @include font_color();
                    opacity: 0.6;
                }
            }
        }

    }
}
</style>